<template>
	<div id="member-profilePassword">
		<public-header title="修改密码"></public-header>
		<div class="content">
			<van-cell-group class="margin-15-t">
				<van-field v-model="password" type="password" placeholder="当前密码" />
				<van-field v-model="newpassword" type="password" placeholder="新密码" />
				<van-field v-model="repassword" type="password" placeholder="确认新密码" />
			</van-cell-group>
			<div class="padding-15" @click="onSubmit">
				<van-button size="normal" block class="bg-primary font-16">确定</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {setData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				password: '',
				newpassword: '',
				repassword: '',
				showPreLoading: true
			}
		},
		components: {
			publicHeader
		},
		methods: {
			onSubmit() {
				var that = this;
				if(!that.password) {
					that.util.$toast('密码不能为空', '', 1000);
					return;
				}
				if(!that.newpassword) {
					that.util.$toast('新密码不能为空', '', 1000);
					return;
				}
				if(!that.repassword) {
					that.util.$toast('请确认密码', '', 1000);
					return;
				}
				setData({
					vue: this,
					url: 'delivery/member/mine/password',
					data: {
						password: that.password,
						newpassword: that.newpassword,
						repassword: that.repassword
					},
					success: function(res){
						var url = that.util.getUrl({path: '/pages/member/mine'});
						that.util.$toast(res, url, 1000);
					}
				})
			}
		},
		mounted(){
			this.showPreLoading = false;
		}
	}
</script>

<style>

</style>


